    -webkit-transform:translate(-50%,-50%);
    transform:translate(-50%,-50%);
}

.ui-mask{
    display: none;
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0);
    -webkit-transition: background .3s;
    transition: background .3s;
}
.ui_mask_show {
    display: block;
    background: rgba(0, 0, 0, 0.6);
}
header{
    position: fixed;
    background: transparent;
    z-index: 100;
    left: 0;
    top:0;
}
.header_inner{
    padding: 0.2rem 0.3rem;
    max-width: 576px;
    width: 100%;
    margin: 0 auto;
    background: #fff;
}
.header_info{
    height: 0.44rem;
}
.header_left{
    background: url(../img/logo.png) no-repeat;
    background-size: cover;
    height: 22px;
    width: 46px;
}
.header_left a{
    display: block;
    width: 100%;
    height: 100%;
    font-size: 0;
}
.header_right a{
    margin-left: 0.25rem;
    font-size: 14px;
    color:#2384E8;
}
.header_right a:nth-child(2){
    color: #9F7860;
}
.header_right a:nth-child(3){
    color: #E4A813;
}
.header_right a:nth-child(4){
    color: #9F7860;
}
.header_right a:nth-child(5){
    display: inline-block;
    width: 24px;
    height: 18px;
    background: url(../img/search.png) no-repeat;
    background-size: cover;
    vertical-align: middle;
    margin-top: 3px;
}

header~div.main{
    padding-top: 0.82rem;
}
nav{
    padding: 0.1rem 0.3rem;
}
nav ul li{
    width: 48%;
    float: left;
    box-sizing: border-box;
    padding: 3px;
}
nav ul li:nth-child(odd){
    margin-right: 4%;
}
nav ul li a{
    background-color: #f6f6f6;
    color: #494949;
    display: block;
    text-align: center;
    line-height: 20px;
    padding: 12px 0;
    border-radius: 2px;
}


/*����*/
.expand-transition {
    transition: transform .2s ease;
    -webkit-transition: transform .2s ease;
    -webkit-transition: -webkit-transform .2s ease;
    -webkit-transform: translateX(0);
}

/* .expand-enter ���������Ŀ�ʼ״̬ */
/* .expand-leave �����뿪�Ľ���״̬ */
.expand-enter{
    -webkit-transform: translateX(100%);
}
.expand-leave{
    opacity: 0;
    height: 0;
    display:none;
}
















